<template>
	<div class="share">
		<div class="head">
			<img src="../images/logoText.png">
			<div @click="show">活动规则</div>
		</div>
		<div class="sharePhone">
			<img src="../images/fuchuang.png">
			<img src="../images/fenxiang.png" @click="LJshare">
		</div>
		<p class="shareT">分享成功后，免费领取pos机!</p>
		<div class="line">
			<img src="../images/line.png">
		</div>
		<div class="daikuan">
			<img src="../images/daikuan.png" @click="daikuan">
		</div>
		<div class="xinyongka">
			<img src="../images/xinyongka.png" @click="xinyongka">
		</div>
		<div class="guize" v-show="isShow">
			<div>
				<h4>活动规则</h4>
				<p>1.活动时间：永久</p>
				<p>2.活动对象：仅限急借号用户（凡是在急借号注册成功的用户均可）</p>
				<p>3.活动规则：急借号用户通过首页活动页面进入活动，将该活动分享给微信或者QQ好友，即可获得POS机一台，分享成功，返回页面填写收货地址，我们将在7个工作日内寄出</p>
				<p>4.活动热线电话：0571-28121882，如有任何疑问欢迎来电咨询</p>
				<p>5.活动最终解释权归急借号所有。</p>
				<span class="btn" @click="hide">我知道了</span>
			</div>
		</div>
		<div class="address" v-show="isModel">
			<div>
				<h1>
					<span class="title">Hi~{{ userName }}</span>
					<span class="detail">请完善以下收件信息,以便我们给您寄件！</span>
				</h1>
				<br>
				<input type="text" v-model="name" placeholder="请填写收货人">
				<br>
				<input type="number" v-model="phone" placeholder="请填写手机号码">
				<br>
				<input type="text" v-model="address" placeholder="请填写收货地址">
				<br>
				<span @click="submit" class="submit">提交</span>
			</div>
		</div>
		<div class="tip" v-show="isTip">
			<div>
				<h1></h1>
				<p>我们已收到您的信息，包裹即将发出~记得查收</p>
				<span @click="hideTip">好的</span>
			</div>
		</div>
		<div class="shareTip" v-show="isShareTip">
			<div>
				<p>点击右上角分享好友或朋友圈，免费来领pos机啦！</p>
				<span @click="shareTip">确定</span>
			</div>
		</div>
	</div>
</template>
<script>
import wx from 'weixin-js-sdk'
import { getWxTicket, findInvite, expressage, getUser } from './../api/api'
import { getStore } from './../config/mUtils.js'
import { Toast } from 'mint-ui'
export default {
	data() {
		return {
			isShow: false, // 活动规则显示开关
			userId: null,
			isModel: false,
			isTip: false,
			isShareTip: false,
			flag: 0,
			title: '', // 分享头
			text: '', // 分享信息
			image: '', // 分享图片
			url: '', // 分享URL
			name: '', // 收货人
			phone: '', // 收货电话
			address: '', // 收货地址
			testoo: '',
			userName: ''
		}
	},
	methods: {
		LJshare() {
			this.isShareTip = true
		},
		shareTip() {
			this.isShareTip = false
		},
		show() {
			this.isShow = true
		},
		hideTip() {
			this.isTip = false
		},
		hide() {
			this.isShow = false
		},
		daikuan() {
			this.$router.push({path: '/attestation'})
		},
		xinyongka() {
			this.$router.push({path: '/card'})
		},
		submit() {
			this.userId = getStore('userId')
			var phoneReg = /^1[3|4|5|7|8][0-9]{9}$/
			if (this.name === '') {
				Toast({
					message: '请输入姓名',
					position: 'middle',
					duration: 2000
				});
			} else {
				if (phoneReg.test(this.phone)) {
					if (this.address === '') {
						Toast({
							message: '请输入收货地址',
							position: 'middle',
							duration: 2000
						});
					} else {
						let params = {
							userId: this.userId,
							consignee: encodeURIComponent(this.name),
							tel: this.phone,
							address: encodeURIComponent(this.address),
							flag: 0,
							mobileType: '3'
						}
						expressage(params).then(res => {
							if (res.code === 200) {
								this.isModel = false
								this.isTip = true
							} else if (res.code === 10) {
								Toast({
									message: res.msg,
									position: 'middle',
									duration: 2000
								});
								this.isModel = false
							}
							console.log(this.name)
							console.log(this.address)
						})
					}
				} else {
					Toast({
						message: '请输入正确的手机号码',
						position: 'middle',
						duration: 2000
					});
				}
			}
		},
		share() {
			var urlCurrent = window.location.href.split('#')[0]
			let params = {
				url: urlCurrent
			}
			getWxTicket(params).then(res => {
				if (res.code === 200) {
					wx.config({
						debug: false,
						appId: res.data.appid,
						timestamp: res.data.timestamp,
						nonceStr: res.data.nonceStr,
						signature: res.data.signature,
						jsApiList: [
							'onMenuShareTimeline',
							'onMenuShareAppMessage',
							'onMenuShareQQ',
							'onMenuShareQZone'
						]
					})
					let self = this
					wx.ready(function() {
						// 分享给朋友
						let params = {
							userId: getStore('userId'),
							flag: 0
						}
						findInvite(params).then(res => {
							if (res.code === 200) {
								var current = res.data
								wx.onMenuShareAppMessage({
									title: current.title,
									desc: current.text,
									link: current.url,
									imgUrl: current.image,
									success: function() {
										self.isShareTip = false
										self.isModel = true
										let params = {
											userId: self.userId
										}
										getUser(params).then(res => {
											if (res.code === 200) {
												if (res.data.realName !== null) {
	    											self.userName = res.data.realName
	    										} else {
	    											self.userName = res.data.phone.substr(0, 3) + '****' + res.data.phone.substr(res.data.phone.length - 4);
	    										}
											}
										})
									},
									cancel: function() {

									}
								})
								// 分享到朋友圈
								wx.onMenuShareTimeline({
									title: current.title,
									link: current.url,
									imgUrl: current.image,
									success: function() {
										self.isShareTip = false
										self.isModel = true
										let params = {
											userId: self.userId
										}
										getUser(params).then(res => {
											if (res.code === 200) {
												if (res.data.realName !== null) {
	    											self.userName = res.data.realName
	    										} else {
	    											self.userName = res.data.phone.substr(0, 3) + '****' + res.data.phone.substr(res.data.phone.length - 4);
	    										}
											}
										})
									},
									cancel: function() {
										
									}
								})
								// 分享到QQ
								wx.onMenuShareQQ({
									title: current.title,
									desc: current.text,
									link: current.url,
									imgUrl: current.image,
									success: function() {
										self.isShareTip = false
										self.isModel = true
										let params = {
											userId: self.userId
										}
										getUser(params).then(res => {
											if (res.code === 200) {
												if (res.data.realName !== null) {
	    											self.userName = res.data.realName
	    										} else {
	    											self.userName = res.data.phone.substr(0, 3) + '****' + res.data.phone.substr(res.data.phone.length - 4);
	    										}
											}
										})
									},
									cancel: function() {
										
									}
								})
								// 分享到QQ空间
								wx.onMenuShareQZone({
									title: current.title,
									desc: current.text,
									link: current.url,
									imgUrl: current.image,
									success: function() {
										self.isShareTip = false
										self.isModel = true
										let params = {
											userId: self.userId
										}
										getUser(params).then(res => {
											if (res.code === 200) {
												if (res.data.realName !== null) {
	    											self.userName = res.data.realName
	    										} else {
	    											self.userName = res.data.phone.substr(0, 3) + '****' + res.data.phone.substr(res.data.phone.length - 4);
	    										}
											}
										})
									},
									cancel: function() {
										
									}
								})
							}
							console.log(res.code)
						})
					})
				}
			})
		}
	},
	mounted() {
		this.share()
	}
}
</script>
<style lang="scss" rel="stylesheet/scss">
	.share {
		width: 100%;
		padding-bottom: 0.5rem;
		background: url(../images/bg.png) repeat-y center;
		background-size: cover;
		z-index: 1000;
		position: relative;
		.head {
			overflow: hidden;
			padding: 0.2rem 0.3rem;
			img {
				width: 1.45rem;
				float: left;
			}
			div {
				float: right;
				color: #fff;
				font-size: 0.26rem;
				padding: 0 0.2rem;
				border: 0.01rem solid #fff;
				background: #4f1e58;
				border-radius: 0.5rem;
				height: 0.5rem;
				line-height: 0.5rem;
			}
		}
		.sharePhone {
			text-align: center;
			margin-top: 0.12rem;
			position: relative;
			img:nth-child(1) {
				width: 4.02rem;
			}
			img:nth-child(2) {
				width: 5.57rem;
				position: absolute;
				left: 50%;
				top: 4.39rem;
				margin-left: -2.785rem;
			}
		}
		.shareT {
			text-align: center;
			font-size: 0.26rem;
			color: #fff;
			letter-spacing: 0.02rem;
			height: 0.28rem;
			line-height: 0.4rem;
			margin-top: 0.2rem;
		}
		.line {
			text-align: center;
			margin-top: 0.1rem;
			img {
				width: 3.65rem
			}
		}
		.daikuan, .xinyongka {
			text-align: center;
			margin-top: 0.1rem;
			img {
				width: 5.08rem;
			}
		}
		.guize {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0,0,0,0.5);
			div {
				width: 5rem;
				border-radius: 0.15rem;
				padding: 0.3rem 0.5rem;
				position: absolute;
				top: 2.56rem;
				left: 50%;
				margin-left: -3rem;
				background: #fff;
				h4 {
					font-size: 0.48rem;
					color: #666;
					text-align: center;
					margin-bottom: 0.1rem;
				}
				p {
					color: #666;
					font-size: 0.24rem;
					line-height: 0.5rem;
				}
				.btn {
					display: inline-block;
					position: relative;
					left: 50%;
					transform: translateX(-50%);
					font-size: 0.28rem;
					color: #fff;
					background: #d34f4f;
					width: 2.28rem;
					line-height: 0.7rem;
					height: 0.7rem;
					text-align: center;
					border-radius: 0.5rem;
					margin-top: 0.2rem;
				}
			}
		}
		.address {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0,0,0,0.5);
			div {
				border-radius: 0.15rem;
				width: 5.98rem;
				position: absolute;
				top: 2.56rem;
				left: 50%;
				margin-left: -2.99rem;
				background: #fff;
				padding-bottom: 0.46rem;
				h1 {
					border-top-left-radius: 0.15rem;
					border-top-right-radius: 0.15rem;
					height: 1.5rem;
					background: url(./../images/shouhuo.png) no-repeat center;
					background-size: cover; 
					position: relative;
					color: #fff;
					.title {
						font-size: 0.28rem;
						position: absolute;
						top: 0.4rem;
						left: 0.3rem;
					}
					.detail {
						font-size: 0.16rem;
						left: 0.3rem;
						top: 0.99rem;
						position: absolute;
					}
				}
				input {
					width: 5.01rem;
					height: 0.78rem;
					background: #f3efef;
					border-radius: 0.15rem;
					margin-top: 0.3rem;
					display: block;
					margin: 0 auto;
					outline: none;
					padding: 0 0.21rem;
					box-sizing: border-box;
					color: #666;
				}
				.submit {
					display: block;
					width: 3.7rem;
					height: 0.8rem;
					line-height: 0.8rem;
					margin: 0.6rem auto 0;
					background-image: linear-gradient(180deg, #fc672c 1%, #fe4a31 65%);
					text-align: center;
					color: #fff;
					font-size: 0.34rem;
					border-radius: 0.3rem;
				}
			}
		}
		.tip {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0,0,0,0.5);
			div {
				border-radius: 0.15rem;
				width: 5.98rem;
				position: absolute;
				top: 2.56rem;
				left: 50%;
				margin-left: -2.99rem;
				background: #fff;
				padding-bottom: 0.46rem;
				h1 {
					background: url(./../images/gou.png) no-repeat center;
					background-size: cover;
					width: 1.4rem;
					height: 1.4rem;
					margin: 0.56rem auto 0.47rem auto;
				}
				p {
					color: #999;
					font-size: 0.24rem;
					text-align: center;
				}
				span {
					width: 3.7rem;
					height: 0.8rem;
					line-height: 0.8rem;
					background-image: linear-gradient(180deg, #fc672c 1%, #fe4a31 65%);
					color: #fff;
					font-size: 0.34rem;
					border-radius: 0.3rem;
					display: block;
					text-align: center;
					margin: 0.3rem auto 0;
				}
			}
		}
		.shareTip {
			position: absolute;
			left: 0;
			top: 0;
			right: 0;
			bottom: 0;
			background: rgba(0,0,0,0.5);
			div {
				border-radius: 0.15rem;
				width: 4rem;
				position: absolute;
				top: 3rem;
				left: 50%;
				transform: translateX(-50%);
				line-height: 0.5rem;
				background: #fff;
				padding: 0.55rem 0.2rem;
				padding-bottom: 0.4rem;
				p {
					font-size: 0.28rem;
					text-align: center;
					color: #999;
					letter-spacing: 0.028rem;
				}
				span {
					width: 3.7rem;
					height: 0.8rem;
					line-height: 0.8rem;
					background-image: linear-gradient(180deg, #fc672c 1%, #fe4a31 65%);
					color: #fff;
					font-size: 0.3rem;
					border-radius: 0.3rem;
					display: block;
					text-align: center;
					margin: 0.5rem auto 0;
				}
			}
		}
	}
</style>



